import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NoContentComponent } from './components';


const routes: Routes = [
  { path: 'task-list', loadChildren: () => import('./modules/tasks/tasks.module').then(m => m.TasksModule) },
  { path: 'application-list', loadChildren: () => import('./modules/applications/applications.module').then(m => m.ApplicationsModule) },
  { path: '', redirectTo: 'task-list', pathMatch: 'full' },
  { path: '**', component: NoContentComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
